<template>
  <v-container>
    <div class="submit-form mt-3 mx-auto">
      <!-- <p class="headline">Add Member</p> -->

      <v-row>
        <v-col class="text-center" cols="12" md="8">
          <v-row class="pa-5">
            <h2>ADD DEPARTMENT</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/depts"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >DEPARTMENT LIST</v-btn
          >
        </v-col>
      </v-row>

      <!-- <div v-if="!submitted"> -->
      <div>
        <v-form ref="form" lazy-validation>
          <v-text-field
            v-model="dept.name"
            :rules="[(v) => !!v || 'Name is required']"
            label="Name"
            required
          ></v-text-field>

          <v-text-field
            v-model="dept.description"
            :rules="[(v) => !!v || 'Description is required']"
            label="Description"
            required
          ></v-text-field>
        </v-form>

        <v-btn
          :disabled="isAddButtonDisabled"
          color="primary"
          class="mt-3"
          @click="saveDept"
          >Submit</v-btn
        >
      </div>
    </div>
  </v-container>
</template>

<script>
import DeptDataService from "../../services/DeptDataService";

export default {
  name: "add-dept",
  data() {
    return {
      dept: {
        id: null,
        name: "",
        description: "",
      },
    };
  },

  computed: {
    isAddButtonDisabled() {
      return !(this.dept.name && this.dept.description);
    },
  },

  methods: {
    saveDept() {
      var data = {
        name: this.dept.name,
        description: this.dept.description,
      };

      DeptDataService.create(data)
        .then((response) => {
          this.dept.id = response.data.id;
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "depts" });
        })
        .catch((e) => {
          console.log(e);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
        });
    },
  },
  mounted() {},
};
</script>
